import React from 'react';
import { ConfigConsumer } from '../config-provider';
import LocaleReceiver from '../locale-provider/LocaleReceiver';
import enUS from './locale/en_US';
export default class FtContainer extends React.Component {
    constructor() {
        super(...arguments);
        this.renderContainer = ({ getPrefixCls }) => {
            const { prefixCls: customizePrefixCls } = this.props;
            const prefixCls = getPrefixCls('container', customizePrefixCls);
            return (<LocaleReceiver defaultLocale={this.getDefaultLocale} componentName="FtContainer">
            {(mergedLocale) => {
                return (<div className={`${prefixCls}`} style={this.props.style}>
                    <div>{mergedLocale.lang['tonow']}</div>
                    <div className={`${prefixCls}--bg`}>{this.props.bg}</div>
                    <div className={`${prefixCls}--content`}>{this.props.con}</div>
                </div>);
            }}
        </LocaleReceiver>);
        };
        this.getDefaultLocale = () => {
            const { locale } = this.props;
            const result = Object.assign(Object.assign({}, enUS), locale);
            result.lang = Object.assign(Object.assign({}, result.lang), (locale || {}).lang);
            return result;
        };
    }
    render() {
        return (<ConfigConsumer>
                {this.renderContainer}
            </ConfigConsumer>);
    }
}
